@import '../../includes/lib';


blockquote {
    @include bidi((
        (border-left, 5px solid $color-gray-20, border-right, 0),
        ));
    @include text-display-md;
    @include zilla-slab;
    margin: $spacing-md auto;
    padding: $spacing-sm $spacing-lg;
    font-weight: bold;

    & > :last-child {
        margin-bottom: 0;
    }
}


